import {
  IconButton,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  Image,
  Button,
  ModalCloseButton,
  Text,
} from "@chakra-ui/react";
import { useDisclosure } from "@chakra-ui/hooks";
import React from "react";
import { ViewIcon } from "@chakra-ui/icons";

const ProfileModel = ({ user, children }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <>
      {children ? (
        <span onClick={onOpen}></span>
      ) : (
        // <IconButton d={{ base: "flex" }} onClick={onOpen} icon={<ViewIcon />} />
        <IconButton d={{ base: "flex" }} onClick={onOpen} icon={<ViewIcon />} />
      )}
      <Modal size="lg" isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay />
        <ModalContent h="410px">
          <ModalHeader
            fontSize="40px"
            fontFamily="Work sans"
            display="flex"
            justifyContent="center"
          >
            {user.name}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody
           display="flex"
           flexDir="column"
           alignItems="center"
           justifyContent="space-between"
          >
            <Image
              borderRadius="full"
              boxSize="170px"
              src={user.pic}
              alt={user.name}
            />
            <Text
              fontSize={{ base: "18px", md: "25px" }}
              fontFamily="Work sans"
              marginTop="20px"
            >
              Email : {user.email}
            </Text>

          </ModalBody>

          <ModalFooter>
            <Button colorScheme="green" mr={2} onClick={onClose}>
              Close
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
};

export default ProfileModel;
